.home-container {
    text-align: center;
    margin: 20px;
}

.movie-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.movie_result_container {
    width: 200px;
    border: 1px solid #ccc;
    padding: 10px;
    text-align: center;
}

.movie_poster {
    width: 100%;
    height: auto;
}

.title_heading {
    font-size: 1.2em;
    margin: 10px 0;
}